.header{
    background-color: rgb(227, 161, 56);
    border-radius:12px ;
    margin: 10px 30px;
    padding: 2px 20px;
}
nav{
    display: flex;
    justify-content: space-between;
    align-items: center;

}
.logo{
    width: 50px;
    aspect-ratio: 1;
    right: 3.5%;
}
.title{
    left:3.5% ;
    font-size: 26px;
    font-weight: bolder;
    color: black;
}
.navLinks {
    list-style: none;
    display: flex;
    gap: 20px;
    margin: 0;
    padding: 0;
  }
  
  .navLinks li {
    font-size: 20px;
    font-weight: bolder;
  }
  
  .navLinks a {
    color: black;
    text-decoration: none;
  }
